.signln-con {
  padding-top: 80px;
  padding-bottom: 80px;
}
.main-bgc {
  background-color: #fff;
  width: 672px;
  height: 620px;
  margin: 0 auto;
  overflow: hidden;
}
.main-bgc .main-tit {
  font-size: 24px;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.85);
  text-align: center;
  margin-top: 48px;
  margin-bottom: 32px;
}
.main-bgc .main-tabs {
  width: 484px;
  margin: 0 auto;
}
.main-bgc .ant-tabs-tab {
  font-size: 18px;
}
.main-bgc .login-form-button {
  width: 100%;
  height: 60px;
  font-size: 18px;
}
.main-bgc .ant-input-affix-wrapper {
  height: 60px;
}
.main-bgc .ant-input-affix-wrapper,
.main-bgc .ant-input-search-button,
.main-bgc .ant-select-selection--single {
  height: 60px;
}
.main-bgc .ant-select-selection-selected-value {
  line-height: 60px;
}
.main-bgc .margin-b {
  margin-bottom: 9px;
}
.main-bgc .operation {
  width: 100%;
  background-color: yellow;
}
.main-bgc .operation div {
  display: inline-block;
}
.main-bgc .forget-password {
  float: left;
}
.main-bgc .forget-register {
  float: right;
}
.main-bgc .ant-tabs-content {
  margin-bottom: 0%;
}
.error-prompt {
  text-align: center;
  color: red;
}
@media only screen and (max-width: 768px) {
  .signln-con {
    padding-top: 50px;
  }
  .main-bgc {
    background-color: #fff;
    width: 80%;
    height: 100%;
    margin: 0 auto;
    overflow: hidden;
  }
  .main-bgc .main-tit {
    font-size: 24px;
    font-weight: bold;
    color: rgba(0, 0, 0, 0.85);
    text-align: center;
    margin-top: 24px;
    margin-bottom: 10px;
  }
  .main-bgc .main-tabs {
    width: 80%;
    margin: 0 auto;
  }
  .main-bgc .login-form-button {
    width: 100%;
    height: 100%;
    font-size: 18px;
  }
  .main-bgc .ant-input-affix-wrapper {
    height: 100%;
  }
  .main-bgc .ant-tabs-content {
    margin-bottom: 10%;
  }
  .main-bgc .ant-select-selection--single {
    height: 100%;
  }
  .main-bgc .ant-select-selection-selected-value {
    line-height: 30px;
  }
}
